<template>
	<view class="wSelect">
		<view @click='openSelect' :style="{width:width,height:height,lineHeight:height,backgroundColor:bgColor}"
		  class="pickerSelect">
			{{value ? value : defaultValue}}
			<view v-if='!value' :class="show?'arrow-up':'arrow'">
			</view>
			<view class="showClose" v-else>
				<image @click.stop="refreshValue"
				  src="data:image/svg+xml;base64,"
				  mode=""></image>
			</view>
		</view>
		<view :style="{width:width}" v-show="show" class="content  animation-top">
			<view class="item" :class="value==item[valueName]?'choose':''" v-for='item in list' :key='item[keyName]'
			  @click="clickSelect(item)"> {{item[valueName]}}
			</view>
		</view>
	</view>
</template>
<script>
	export default {
		name: 'wSelect',
		props: {
			width: {
				type: String,
				default: '300rpx'
			},
			bgColor: {
				type: String,
				default: '#fff'
			},
			height: {
				type: String,
				default: '60rpx'
			},
			//默认显示的内容
			defaultValue: {
				type: String,
				default: '所有人'
			},
			//绑定的唯一id
			keyName: {
				type: String,
				default: 'id'
			},
			//显示的内容
			valueName: {
				type: String,
				default: 'value'
			},
			list: {
				type: Array,
				default: () => []
			},
			//双向绑定的值
			value: {
				default: ''
			}
		},
		data() {
			return {
				show: false
			};
		},
		methods: {
			openSelect() {
				this.show = !this.show
			},
			clickSelect(item) {
				this.$emit('input', item[this.valueName])
				this.$emit('change', item)
				this.show = false
			},
			refreshValue() {
				this.$emit('input', '')
				this.$emit('change', '')
				this.show = false
			}
		},
	}
</script>
<style lang="scss" scoped>
	.wSelect {
		.pickerSelect {
			background-color: #EEEEEE;
			border-radius: 6rpx;
			padding: 0 20rpx;
			position: relative;
			transition: all .6s;

			.showClose {
				width: 30rpx;
				height: 30rpx;
				position: absolute;
				right: 14rpx;
				top: 10%;

				image {
					width: 100%;
					height: 100%;
				}
			}

			.arrow {
				transition: all .6s;
				position: absolute;
				top: 32%;
				right: 20rpx;
				border-left: 2rpx solid #999999;
				border-bottom: 2rpx solid #999999;
				height: 16rpx;
				width: 16rpx;
				transform: translate(2rpx, -2rpx) rotate(-45deg);
				-webkit-transform: translate(2rpx, -2rpx) rotate(-45deg);
				border-right: 2rpx solid transparent;
				border-top: 2rpx solid transparent;
				display: inline-block;
			}

			.arrow-up {
				transition: all .6s;
				position: absolute;
				top: 50%;
				right: 20rpx;
				border-left: 2rpx solid #999999;
				border-bottom: 2rpx solid #999999;
				height: 16rpx;
				width: 16rpx;
				transform: translate(2rpx, -2rpx) rotate(135deg);
				-webkit-transform: translate(2rpx, -2rpx) rotate(135deg);
				border-right: 2rpx solid transparent;
				border-top: 2rpx solid transparent;
				display: inline-block;
			}
		}

		.content {
			position: absolute;
			margin-top: 20rpx;
			padding: 20rpx;
			border-radius: 4px;
			background-color: #fff;
			box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
			z-index: 999;

			&::before {
				content: '';
				position: absolute;
				left: 26rpx;
				top: -10rpx;
				width: 0;
				height: 0;
				border-bottom: 10rpx solid #fff;
				border-left: 10rpx solid transparent;
				border-right: 10rpx solid transparent;
			}

			.item {
				padding: 10rpx;
				height: 60rpx;
				line-height: 40rpx;
				margin-bottom: 10rpx;
			}

			.choose {
				background-color: #f5f7fa;
			}
		}

		.animation-top {
			animation-name: slide-top;
			animation-duration: .5s;
			animation-timing-function: ease-out;
			animation-fill-mode: both;
		}

		@keyframes slide-top {
			0% {
				opacity: 0;
				transform: translateY(-30%);
			}

			100% {
				opacity: 1;
				transform: translateY(0);
			}
		}
	}
</style>
